.main-section {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #FFFFFF;
  padding: 18.75rem 0 0 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    font-family: Arimo;
  }
  &__left {
    position: relative;
    z-index: 1;
  }
  &__right {
    position: relative;
    z-index: 1;
    flex-shrink: 0;
  }
  &__group {
    margin-top: 2rem;
  }
  &__subtitle {
    font-size: 1.25rem;
    line-height: 130%;
  }
  &__countries {
    max-width: 24rem;
    display: flex;
    flex-wrap: wrap;
    gap: 0.625rem;
    margin-top: 0.75rem;
    i {
      max-width: 4rem;
    }
    p {
      display: inline-flex;
      align-items: center;
      gap: 0.625rem;
      font-size: 1.875rem;
      line-height: 130%;
      margin: 0;
    }
  }
  &__group2 {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-top: 2rem;
  }
  &__info {
    font-size: 0.875rem;
    line-height: 130%;
    strong {
      font-weight: 700;
    }
  }
  &__advantages {
    max-width: 19.6875rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875rem;
    margin-left: auto;
  }
  &__advantage {
    position: relative;
    display: flex;
    align-items: center;
    background: rgba(44, 39, 12, 0.5);
    border-radius: 1.25rem;
    padding: 0.625rem;
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 60%;
      height: 0.0625rem;
      background: linear-gradient(90deg, rgba(0, 0, 0, .1) 0, #fff 49%, rgba(0, 0, 0, .1) 100%);
      pointer-events: none;
    }
    &:before {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 60%;
      height: 0.0625rem;
      background: linear-gradient(90deg, rgba(0, 0, 0, .1) 0, #fff 49%, rgba(0, 0, 0, .1) 100%);
      pointer-events: none;
    }
    &-icon {
      max-width: 4.375rem;
      flex-shrink: 0;
    }
    &-text {
      font-size: 1.125rem;
      line-height: 130%;
    }
  }
  &__img {
    position: absolute;
    top: 38%;
    left: 50%;
    transform: translate(-50%, -50%);
   img:nth-child(1) {
     width: 42.625rem;
     max-width: unset;
     max-height: unset;
   }
   .gif {
     position: absolute;
     right: -1.5rem;
     bottom: 11.875rem;
     width: 16.5rem;
     pointer-events: none;
     img {
       width: 100%;
       max-width: 100%;
       max-height: 100%;
       mix-blend-mode: plus-lighter;
     }
   }
    .gif2 {
      position: absolute;
      left: -3rem;
      bottom: 11.875rem;
      width: 16.5rem;
      pointer-events: none;
      img {
        width: 100%;
        max-width: 100%;
        max-height: 100%;
        mix-blend-mode: plus-lighter;
      }
    }
  }
  &__items {
    max-width: 51.25rem;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 7rem / 3));
    gap: 3.5rem;
    border-radius: 2.5rem 2.5rem 0 0;
    padding: 0 4.375rem 0.625rem;
    margin: 17.8125rem auto 0;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-icon {
      width: 7.5rem;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-size: 1rem;
      line-height: 130%;
      color: rgb(51, 51, 51);
      margin-top: 1.25rem;
      strong {
        display: block;
        font-weight: 700;
        margin-bottom: 1.25rem;
      }
      p {
        margin: 0;
      }
    }
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    top: -3.125rem;
    right: -23.125rem;
    width: 79.375rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.prices {
  padding: 9.375rem 0;
