}
.price-card {
  position: relative;
  display: flex;
  flex-direction: column;
  background-image: url("../img/price-card__bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 1.5rem;
  color: #fff;
  padding: 3.125rem 3.125rem 18.75rem;
  &__desc {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__title {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
  }
  &__subtitle {
