h1,
h2 {
  font-size: 3rem;
  line-height: 130%;
  margin: 0;
  .text_color {
    color: rgb(253, 206, 109);
  }
}
h2 {
  .text_color {
    background: radial-gradient(114.00% 38.00% at 50% 50%,rgb(253, 206, 109),rgb(85, 62, 22) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
.btn-orange {
  position: relative;
  background: radial-gradient(circle, rgba(253,206,109,1) 0%, rgba(85,62,22,1) 70%);
  box-shadow: 0 0.625rem 1.25rem 0 rgba(253, 206, 109, 0.36);
  border-radius: 0.625rem;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 70%;
    height: 110%;
    background: linear-gradient(0.00deg, rgb(255, 255, 255) -0.004%,rgb(255, 255, 255) 99.996%);
    opacity: 0.2;
    clip-path: polygon(0 0, 100% 0%, 75% 100%, 0% 100%);
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    width: 60%;
    height: 120%;
    background: linear-gradient(0.00deg, rgb(255, 255, 255) -0.004%,rgb(255, 255, 255) 99.996%);
    clip-path: polygon(25% 0, 100% 0%, 100% 100%, 0 100%);
    pointer-events: none;
    opacity: 0;
  }
  span {
    position: relative;
    z-index: 2;
    min-width: auto;
    padding: 1.5rem 2rem;
    font-family: Arimo;
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    color: #fff;
  }
  &:hover {
    &:after {
      opacity: 0;
    }
    &:before {
      opacity: 0.2;
    }
  }
}
.agreement-check {
  input {
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
  input:checked ~ label {
    &:after {
      background-image: url("../img/check.svg");
    }
  }
  label {
    font-size: 0.75rem;
    color: rgb(255, 255, 255);
    padding-left: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0.0625rem;
      left: 0;
      width: 0.875rem;
      height: 0.875rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      border: 0.0625rem solid rgb(253, 206, 109);
      border-radius: 0.3125rem;
    }
    a {
      position: relative;
      z-index: 3;
      color: rgb(253, 206, 109);
      border-bottom: 0.0625rem solid rgb(253, 206, 109);
      &:hover {
        color: rgb(229, 225, 218);
        border-color: #fff;
      }
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  width: 3rem;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(255, 255, 255);
  border: 0.125rem solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  opacity: 1 !important;
  &:after {
    display: none;
  }
  &:hover {
    background: radial-gradient(circle, rgba(253,206,109,1) 0%, rgba(85,62,22,1) 100%);
    border-color: #553e16;
    svg path {
      stroke: #fff;
    }
  }
  svg {
    width: 0.5rem;
    height: auto;
  }
}
.left-anim {
  transform: translate(-40%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(40%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.navigation {
  padding: 3.125rem 0;
  &__inner {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    font-size: 1.5rem;
    line-height: 130%;
    a:hover {
      color: #ffbc30;
    }
  }
}


//Animation
// Core
$core_width: 50px;
$core_color:#de0b46;
$core_glow:0px 0px 70px 20px #de0b46;

// Outer 1
$outer_one_offset: 120px;
$outer_one_width: 10px;
$outer_one_height: 30px;
$outer_one_color: #de0b46;
$outer_one_glow: 0px 0px 20px 0px #de0b46;

// Outer 2
$outer_two_offset: 290px;
$outer_two_width: 28px;
$outer_two_height: 5px;
$outer_two_color: #de0b46;
$outer_two_glow: 0px 0px 20px 0px #de0b46;

// Outer 3
$outer_three_offset: 440px;
$outer_three_width: 55px;
$outer_three_height: 5px;
$outer_three_color: #55E2F9;
$outer_three_glow: 0px 0px 20px 0px #55E2F9;

// Outer 4
$outer_four_offset: 440px;
$outer_four_width: 6px;
$outer_four_height: 5px;
$outer_four_color: #de0b46;
$outer_four_glow: 0px 0px 20px 0px #de0b46;

// Outer 5
$outer_five_offset: 410px;

// Menu items
$menu_offset: 540px;
$menu_height: 20px;
$menu_color: transparent;
$menu_glow: 0px 0px 25px rgba(222, 11, 70, 0.27);

/* Placeholders */

%center{
  left:0;
  right:0;
  top:40%;
  position:absolute;
  z-index: 2;
  margin:auto;
  transform:translateY(-50%);
}

%decal{
  position:absolute;
  width:$core_width;
  height:$core_width;
  left:50%;
}

%fade_loop{
  @for $i from 1 through 36{
    &:nth-of-type(#{$i}){
      animation:fade 5s ($i/10 + 0s) infinite;
    }
  }
}

%fade_loop_alt{
  @for $i from 1 through 36{
    &:nth-of-type(#{$i}){
      animation:fade 7s ($i/10 + 0s) infinite;
    }
  }
}

%circ_loop{
  position: absolute;
  @for $i from 1 through 36{
    &:nth-of-type(#{$i}){
      border-radius:2px;
      transform:rotateZ($i*10 + (0deg));
    }
  }
}

%circ_loop_quart{
  position: absolute;
  @for $i from 1 through 36{
    &:nth-of-type(#{$i}){
      border-radius:2px;
      transform:rotateZ($i*4 + (0deg));
    }
  }
}

%circ_loop_quart_alt{
  position: absolute;
  @for $i from 1 through 36{
    &:nth-of-type(#{$i}){
      border-radius:2px;
      transform:rotateZ($i*4 + (0deg) - 180);
    }
  }
}

.intro{

}
body{
  .grain{
    transform:scale(1) !important;
    background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/217233/overlay.png");
    left: 0;
    right: 0;
    margin: auto;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 100;
    pointer-events: none;
    top: 0;
    opacity: 1;
    bottom: 0;
  }
  .center{
    @extend %center;
    transform: translateY(-21px) translateZ(-870px) rotateX(69deg) rotateY(-26deg) rotatez(-647deg) scale(0.9);
    height:$core_width;
    width:$core_width;
    animation:wiggle 20s infinite linear;
    .core{
      width:$core_width;
      height:$core_width;
      border-radius:$core_width;
      background:$core_color;
      box-shadow:$core_glow;
    }
    .outer_one{
      @extend %decal;
      transform:translateX(-$outer_one_offset) translateY(-50%);
      &__piece{
        width: $outer_one_width;
        height: $outer_one_height;
        background: $outer_one_color;
        box-shadow:$outer_one_glow;
        transform-origin: $outer_one_offset 0px;
        @extend %circ_loop;
      }
    }
    .outer_two{
      @extend %decal;
      transform:translateX(-$outer_two_offset) translateY(-50%);
      &__piece{
        width: $outer_two_width;
        height: $outer_two_height;
        background: $outer_two_color;
        box-shadow:$outer_two_glow;
        transform-origin: $outer_two_offset 0px;
        @extend %circ_loop;
        @extend %fade_loop;
      }
    }
    .outer_three{
      @extend %decal;
      transform:translateX(-$outer_three_offset) translateY(-50%);
      &__piece{
        width: $outer_three_width;
        height: $outer_three_height;
        background: $outer_three_color;
        box-shadow:$outer_three_glow;
        transform-origin: $outer_three_offset 0px;
        @extend %circ_loop_quart;
        @extend %fade_loop;
      }
    }
    .outer_four{
      @extend %decal;
      transform:translateX(-$outer_four_offset) translateY(-50%);
      &__piece{
        width: $outer_four_width;
        height: $outer_four_height;
        background: $outer_four_color;
        box-shadow:$outer_four_glow;
        transform-origin: $outer_four_offset 0px;
        @extend %circ_loop_quart_alt;
        @extend %fade_loop_alt;
      }
    }
    .outer_five{
      @extend %decal;
      transform:translateX(-$outer_five_offset) translateY(-50%);
      &__piece{
        width: $outer_four_width;
        height: $outer_four_height;
        background: $outer_four_color;
        box-shadow:$outer_four_glow;
        transform-origin: $outer_five_offset 0px;
        @extend %circ_loop_quart_alt;
        @extend %fade_loop_alt;
      }
    }
    .pieces{
      @extend %decal;
      transform:translateX($menu_offset) translateY(-50%);
      .tip{
        font-size: 17px;
        position: absolute;
        left: 105%;
        opacity:0;
        transition:all .2s .3s;
        font-weight: 400;
        color: #85DFE4;
        width: 100%;
        top: 70px;
      }
      span{
        position: absolute;
        top: 19px;
        width: 270px;
        LEFT: 34PX;
        transform: translateZ(-770px) rotateZ(0deg);
        transition:all .2s .1s;
      }
      .line{
        width: 0px;
        height: 5px;
        left: -547px;
        transform: rotatez(4deg);
        top: 17px;
        background: #DE0B46;
        position: absolute;
        opacity:0;
      }
      .future_ui__piece{
        border: 3px solid #DE0B46;
        position: absolute;
        font-weight:900;
        padding:30px;
        font-size:30px;
        box-shadow: 0px 0px 25px rgba(222, 11, 70, 0.27);
        transform-style: preserve-3d;
        transition: all 1s .1s;
        color: white;
        text-align: left;
        @for $i from 1 through 36{
          &:nth-of-type(#{$i}){
            transform:rotateZ($i*10 + (0deg) - 55) rotateY(0deg);
            width: 220 + random(73) + px;
            &:hover{
              cursor:pointer;
              color:white;
              animation:blink .4s .4s;
              background:#DE0B46;
              transform:rotate($i*10 + (0deg) - 55) translatex(20px);
              box-shadow: 0px 0px 55px rgba(222, 11, 70, 0.77);
              span{
                position: absolute;
                top: 12px;
                width: 270px;
                text-shadow: 0px 13px 0px rgba(30, 30, 30, 0.4);
                left:240px;
                transform: translateZ(100px) rotateZ(0deg);
              }
              .tip{
                opacity:1;
                left: 110%;
              }
              .line{
                opacity:1;
                width:547px;
                transition:all .4s .2s;
              }
            }
          }
        }
        height: $menu_height;
        background: $menu_color;
        color:white;
        border-radius:100px !important;
        box-shadow:$menu_glow;
        transform-origin: - $menu_offset 0px;
        @extend %circ_loop;
      }
      .blank{
        opacity:0.1;
        background:none;
        pointer-events:none;
        box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.27);
        border:6px solid #3D4B6D !important;
      }
    }
  }
}

/* animations */

@keyframes spin{
  0%{transform:rotate(0deg)}
  100%{transform:rotate(360deg)}
}

@keyframes fade{
  0%{opacity:0;}
  50%{opacity:1;}
  100%{opacity:0;}
}

@keyframes blink{
  0%{opacity:1}
  25%{opacity:0}
  50%{opacity:1}
  75%{opacity:0}
  100%{opacity:1}
}
@keyframes in{
  0%{opacity:0}
  100%{opacity:1}
}
@keyframes inalt{
  0%{opacity:0}
  50%{opacity:1}
  100%{opacity:0}
}
@keyframes wiggle{
  0%{transform: translateY(0) translateZ(-840px) rotateX(64deg) rotateY(-21deg) rotatez(80deg) scale(0.9);}
  25%{transform: translateY(0) translateZ(-820px) rotateX(66deg) rotateY(-24deg) rotatez(84deg) scale(0.9);}
  50%{transform: translateY(0) translateZ(-840px) rotateX(62deg) rotateY(-23deg) rotatez(82deg) scale(0.9);}
  75%{transform: translateY(0) translateZ(-840px) rotateX(63deg) rotateY(-19deg) rotatez(78deg) scale(0.9);}
  100%{transform: translateY(0) translateZ(-840px) rotateX(64deg) rotateY(-21deg) rotatez(80deg) scale(0.9);}
}

@keyframes intro_box{
  0%{opacity:0;transform:scale(0)}
  20%{opacity:1;transform:scale(1.2)}
  40%{transform:scale(0.89)}
  60%{transform:scale(1.04)}
  80%{transform:scale(0.98)}
  100%{opacity:1;transform:scale(1)}
}


@media screen and (max-width: 1399px) {
  h1,
  h2 {
    font-size: 2.5rem !important;
  }
}

@media screen and (max-width: 1023px) {
  h1,
  h2 {
    font-size: 2rem !important;
  }
  .btn-orange {
    span {
      font-size: 1rem;
    }
  }
}