#footer {
  position: relative;
  padding: 0;
}
.footer2 {
  .footer-top {
    &:after {
      display: none;
    }
  }
}
.footer-top {
  position: relative;
  padding: 6.25rem 0 2.5rem;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 60%;
    height: 0.125rem;
    background: linear-gradient(90deg, rgba(0, 0, 0, .1) 0, #fff 49%, rgba(0, 0, 0, .1) 100%);
    pointer-events: none;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__left {
    font-family: Arimo;
  }
  &__logo {
    width: 16.5rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 16.5rem;
    font-size: 1rem;
    line-height: 130%;
    margin: 2rem 0 0 0;
  }
  .b2b {
    max-width: 13.125rem;
    width: 100%;
    margin-top: 2rem;
    img {
      width: 100%;
    }
  }
  &__text2 {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem 0.875rem;
    margin-top: 2rem;
    p {
      font-size: 0.75rem;
      line-height: 130%;
      margin: 0;
    }
  }
  &__items {
    display: flex;
    gap: 6.25rem;
  }
  &__subtitle {
    font-family: AlegreyaSans;
    font-size: 1.875rem;
    line-height: 130%;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75rem;
    margin-top: 1.25rem;
    li {
      &:after,
      &:before {
        display: none;
      }
      a {
        font-family: Arimo;
        font-size: 1.25rem !important;
        line-height: 130%;
        border: none !important;
        padding: 0 !important;
      }
      a:hover {
        color: #FDCE6D;
      }
    }
  }
  &__group {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 0.25rem;
  }
  &__phone {
    font-family: AlegreyaSans;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: rgb(253, 206, 109);
    }
  }
  &__btn {
    background: transparent;
    &:hover {
      span {
        border-color: #fff;
        color: #fff;
      }
    }
    span {
      min-width: unset;
      display: inline-block;
      font-family: AlegreyaSans;
      font-size: 1rem;
      line-height: 130%;
      font-weight: 500;
      color: rgb(253, 206, 109);
      border-bottom: 0.0625rem dashed rgb(253, 206, 109);
      padding: 0;
      transition: all 0.3s;
    }
  }
  &__group2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.75rem;
    margin-top: 2.875rem;
  }
  &__location,
  &__email,
  &__time {
    display: flex;
    align-items: center;
    gap: 0.625rem;
    &:hover {
      color: rgb(253, 206, 109);
      i {
        background: radial-gradient(circle, rgba(253,206,109,1) 0%, rgba(85,62,22,1) 100%);
      }
    }
    i {
      width: 2.625rem;
      height: 2.625rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgb(253, 206, 109);
      border: 0.1875rem solid rgba(255, 255, 255, 0.2);
      border-radius: 50%;
    }
    p {
      text-align: start;
      font-size: 1rem;
      line-height: 130%;
      margin: 0 !important;
    }
    strong {
      display: block;
      font-family: Arimo;
      font-weight: 500;
      margin-bottom: 0.25rem;
    }
  }
  &__location {
    &:hover {
      i {
        svg path {
          stroke: #fff;
        }
      }
    }
  }
  &__email,
  &__time {
    &:hover {
      i {
        svg path {
          fill: #fff;
        }
      }
    }
  }
  &__time {
    &:hover {
      i {
        svg path {
          fill: #fff;
          stroke: #fff;
        }
      }
    }
  }
  &__socials {
    padding-left: 3.25rem;
    margin-top: 1.5rem;
    &-text {
      font-family: Arimo;
      font-size: 1rem;
      line-height: 130%;
    }
    &-group {
      display: flex;
      gap: 1.25rem;
      margin-top: 0.875rem;
    }
  }
  &__social {
    width: 2.125rem;
    height: 2.125rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .telegram {
    background: #FDCE6D;
    border-radius: 50%;
    &:hover {
      background: rgb(255, 188, 48);
      svg path {
        fill: #fff;
      }
    }
  }
  .whatsapp {
    &:hover {
      svg path:nth-child(2) {
        fill: rgb(255, 188, 48);
      }
      svg path:nth-child(3) {
        fill: #fff;
      }
    }
  }
  .vk {
    background: #FDCE6D;
    border-radius: 0.5rem;
    &:hover {
      background: rgb(255, 188, 48);
      svg path {
        fill: #fff;
      }
    }
  }
  .instagram {
    &:hover {
      svg path {
        fill: #fff;
      }
    }
  }
}
.footer-bottom {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 0);
  width: 100%;
  padding: 1.5rem 0;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    gap: 1rem 12.5rem;
    p {
      font-family: Arimo;
      font-size: 0.75rem;
      line-height: 130%;
      color: #25282A;
      margin: 0;
    }
  }
}

@media screen and (max-width: 1600px) {
  .footer-top {
    padding-top: 2.5rem;
    &__items {
      gap: 3.125rem;
    }
    &__subtitle {
      font-size: 1.5rem;
    }
    ul {
      li {
        a {
          font-size: 1rem !important;
        }
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  #footer {
    background: #000;
  }
  .footer-bottom {
    position: static;
    transform: unset;
    background: #fff;
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    &__text {
      max-width: 14rem;
      font-size: 0.875rem;
    }
    &__text2 {
      flex-direction: column;
    }
    &__subtitle {
      font-size: 1.25rem;
    }
    ul {
      li {
        a {
          font-size: 0.875rem !important;
        }
      }
    }
    &__location,
    &__email,
    &__time {
      i {
        width: 2rem;
        height: 2rem;
      }
      p {
        font-size: 0.875rem;
      }
    }
    &__socials {
      padding-left: 2.625rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    &__inner {
      gap: 1.5rem;
    }
    &__logo {
      width: 12.5rem;
    }
    &__text {
      margin-top: 1rem;
    }
    .b2b {
      max-width: 11.25rem;
      margin-top: 1rem;
    }
    &__text2 {
      margin-top: 1rem;
    }
    &__items {
      display: grid;
      grid-template-columns: 100%;
      gap: 1.5rem;
    }
    &__group2 {
      gap: 1.5rem;
      margin-top: 2rem;
    }
  }
  .footer-bottom {
    &__inner {
      gap: 1rem 3rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      flex-wrap: wrap;
    }
    &__center {
      order: 1;
    }
  }
}