#header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0;
  .menu {
    display: flex;
    justify-content: center;
    gap: 1.25rem;
  }
  .menu-item {
    &--active {
      a {
        color: rgb(253, 206, 109);
      }
    }
    &:after,
    &:before {
      display: none;
    }
    a {
      display: flex;
      align-items: center;
      gap: 0.625rem;
      font-family: AlegreyaSans;
      font-size: 1.25rem;
      line-height: 130%;
      padding: 0;
      border: unset;
      &:hover {
        color: rgb(253, 206, 109);
        i svg path {
          stroke: rgb(253, 206, 109);
        }
      }
      i {
        display: flex;
        align-items: center;
      }
    }
  }
  .sub-menu {
    z-index: 2;
    background: #000;
  }
}
.header2 {
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(1.25rem);
}
.header-top {
  color: #fff;
  padding: 1.5rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 3.75rem;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 13.625rem;
  }
  &__logo {
    width: 16.5625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-family: Arimo;
    font-size: 1rem;
    line-height: 130%;
  }
  &__location {
    display: flex;
    align-items: center;
    gap: 0.625rem;
    &:hover {
      color: rgb(253, 206, 109);
      i {
        background: radial-gradient(circle, rgba(253,206,109,1) 0%, rgba(85,62,22,1) 100%);
        svg path {
          stroke: #fff;
        }
      }
    }
    i {
      width: 2.625rem;
      height: 2.625rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgb(253, 206, 109);
      border: 0.1875rem solid rgba(255, 255, 255, 0.2);
      border-radius: 50%;
    }
    p {
      text-align: start;
      font-family: AlegreyaSans;
      font-size: 1rem;
      line-height: 130%;
      margin: 0 !important;
    }
    strong {
      display: block;
      font-weight: 500;
      margin-bottom: 0.25rem;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 1.5rem;
  }
  &__socials {
    display: flex;
    gap: 0.935rem;
  }
  &__social {
    width: 2.125rem;
    height: 2.125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(253, 206, 109);
    border-radius: 50%;
    &:hover {
      background: rgb(255, 188, 48);
    }
    svg {
      width: 100%;
      height: auto;
    }
  }
  .telegram {
    background: rgb(255, 188, 48);
    &:hover {
      svg path {
        fill: #fff;
      }
    }
    svg {
      width: 1.25rem;
      height: auto;
    }
  }
  .whatsapp {
    background: transparent;
    &:hover {
      svg path:nth-child(2) {
        fill: rgb(255, 188, 48);
      }
      svg path:nth-child(3) {
        fill: #fff;
      }
    }
  }
  &__group2 {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 0.25rem;
  }
  &__phone {
    font-family: AlegreyaSans;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: rgb(253, 206, 109);
    }
  }
  &__btn {
    background: transparent;
    &:hover {
      span {
        border-color: #fff;
        color: #fff;
      }
    }
    span {
      min-width: unset;
      display: inline-block;
      font-family: AlegreyaSans;
      font-size: 1rem;
      line-height: 130%;
      font-weight: 500;
      color: rgb(253, 206, 109);
      border-bottom: 0.0625rem dashed rgb(253, 206, 109);
      padding: 0;
      transition: all 0.3s;
    }
  }
  .menu {
    display: none !important;
  }
}
.header-bottom {
  position: relative;
  color: #fff;
  padding: 0.875rem 0;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 60%;
    height: 0.125rem;
    background: linear-gradient(90deg, rgba(0, 0, 0, .1) 0, #fff 49%, rgba(0, 0, 0, .1) 100%);
    pointer-events: none;
  }
  &:before {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 60%;
    height: 0.125rem;
    background: linear-gradient(90deg, rgba(0, 0, 0, .1) 0, #fff 49%, rgba(0, 0, 0, .1) 100%);
    pointer-events: none;
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 2.5rem;
  height: 1.5625rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: #fff;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.6875rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.6875rem;
      transform: rotate(45deg);
    }
  }
}


@media screen and (max-width: 1600px) {
  .header-top {
    &__right {
      gap: 3.75rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  #header {
    .menu-item {
      a {
        font-size: 1.125rem;
      }
    }
  }
  .header-top {
    &__left {
      gap: 2rem;
    }
    &__right {
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .header-top {
    &__logo {
      width: 14rem;
    }
    &__text {
      font-size: 0.875rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    .menu-item {
      a {
        font-size: 1rem;
      }
    }
  }
  .header-top {
    padding: 1rem 0;
    &__right {
      gap: 1.5rem;
    }
    &__text {
      display: none;
    }
    &__location {
      i {
        width: 2.125rem;
        height: 2.125rem;
      }
      p {
        font-size: 0.875rem;
      }
    }
    &__phone {
      font-size: 1.125rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .header-top {
    &__right {
      position: fixed;
      z-index: 10;
      top: -1.5rem;
      left: -105%;
      width: 100%;
      height: calc(100vh + 1.5rem);
      flex-direction: column;
      text-align: center;
      background: #000;
      padding: 7.5rem 1rem 5rem;
      overflow-y: auto;
      transition: all 0.5s;
      &--active {
        left: 0;
      }
    }
    &__text {
      display: block;
    }
    &__group {
      flex-direction: column;
    }
    .menu {
      display: flex !important;
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 1rem !important;
    }
    .menu-item {
      a i {
        display: none !important;
      }
    }
    .sub-menu {
      display: none !important;
    }
  }
  .header-bottom {
    display: none;
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    &__text {
      display: none;
    }
  }
}